
import React from 'react';

import { Separator, Tabbar, TabbarItem, Placeholder, CardScroll, Avatar, Counter, Card, CardGrid, platform, IOS, Cell, Panel, Group, PanelHeader, PanelHeaderButton } from '@vkontakte/vkui';

//иконки назад
import Icon28ReplyOutline from '@vkontakte/icons/dist/28/reply_outline';
import Icon28LogoVkOutline from '@vkontakte/icons/dist/28/logo_vk_outline';
import Icon24LogoVk from '@vkontakte/icons/dist/24/logo_vk';
//иконки
import Icon28AirplayVideoOutline from '@vkontakte/icons/dist/28/airplay_video_outline'; //обучение
import Icon28LocationOutline from '@vkontakte/icons/dist/28/location_outline'; //такси
import Icon28BugOutline from '@vkontakte/icons/dist/28/bug_outline'; //covid
import Icon28NewsfeedOutline from '@vkontakte/icons/dist/28/newsfeed_outline'; //новости
import Icon28HomeOutline from '@vkontakte/icons/dist/28/home_outline'; //организации


const osname = platform();


const Covid = props => (
    <Panel id={props.id}>
        <PanelHeader
        left={<PanelHeaderButton>
        {osname === IOS ? <Icon28LogoVkOutline/> : <Icon24LogoVk/>}
    </PanelHeaderButton>}
        >
         COVID-19
        </PanelHeader>
        <Group>

        <CardScroll>
        <Card size="l">
            <Placeholder
            icon={<Avatar size={45} mode="image" src="https://sun1-23.userapi.com/YZNJNpbIeEVuBpEPdkrbKMpNGnmhTrxLPLovnw/rSKlCwHEF-0.jpg" />}
            header="Выздоровело: 8"
          >
            Количество выписанных
            <br/>
            за
            <br/>
            весь период.
          </Placeholder>
          <div style={{ width: 96, height: 1 }} />
        </Card>
        <Card size="l">
        <Placeholder
            icon={<Avatar size={45} mode="image" src="https://sun9-26.userapi.com/c857736/v857736115/1cc8af/_V7V3jO-sj4.jpg" />}
            header="Заражено: 31"
          >
            Количество людей,
            <br/>
            которые
            <br/>
            находятся на лечении.
          </Placeholder>
          <div style={{ width: 96, height: 1 }} />
        </Card>
        <Card size="l">
        <Placeholder
            icon={<Avatar size={45} mode="image" src="https://sun1-30.userapi.com/pME0o0gSVhF5cA2AmBKOicUPW0yA_Imz794WXQ/S8J8BT4OjKs.jpg" />}
            header="Умерло: 0"
          >
            Количество умерших
            <br/>
            людей за
             <br/>
             весь период.
          </Placeholder>
          <div style={{ width: 96, height: 1 }} />
        </Card>
      </CardScroll>
      <br/>
      <br/><CardGrid>
          <Card size="l" mode="shadow">
          <Cell before={<Avatar size={28} mode="image" src="https://sun1-28.userapi.com/dV8-DvAWfasJ-kyX129bGR0_t_I1AQ_BvDctoQ/pAamsaQq9ZE.jpg" />} indicator={<Counter mode="primary">16:54</Counter>}>Последнее обновление:</Cell>
            <div style={{ height: 1 }} />
          </Card>
        </CardGrid>


    </Group>
    <br/>
    <br/>
    <br/>
    <br/>
    <br/>
    <br/>
            <Tabbar>
             <Separator wide />
                      <TabbarItem text="Организ." onClick={props.go} data-to="organizations"><Icon28HomeOutline />
                      </TabbarItem>
                      <TabbarItem text="Новости" onClick={props.go} data-to="home"><Icon28NewsfeedOutline />
                      </TabbarItem>
                      <TabbarItem text="COVID" onClick={props.go} data-to="covid"><Icon28BugOutline />
                      </TabbarItem>
                      <TabbarItem text="Обучение" onClick={props.go} data-to="education"><Icon28AirplayVideoOutline />
                      </TabbarItem>
                      <TabbarItem text="Такси" onClick={props.go} data-to="taxi"><Icon28LocationOutline />
                      </TabbarItem>
          </Tabbar>
    </Panel>
);

export default Covid;
